@font-face {
  font-family: 'Roboto Regular';
  src: url('/fonts/Roboto-Regular.ttf');
}
@font-face {
  font-family: 'FontAwesome';
  src: url('/fonts/fontawesome-webfont.ttf');
}
body {
  background: #F2F1EF;
  font-family: "Roboto Regular" !important;
}
.login,
.register {
  background: #fff;
  width: 350px;
  height: auto;
  margin: 3% auto;
  padding: 40px 50px;
  text-align: center;
}
input,
select {
  height: 35px;
  width: 300px;
  font-size: 16px;
  padding: 5px 15px;
  margin: 10px;
  background: #EDEEF0;
  border: 0;
  color: #6C7A89;
  -webkit-box-shadow: inset 0 0 0 50px #EDEEF0;
  -webkit-text-fill-color: #6C7A89;
  font-family: "Roboto Regular" !important;
}
input:focus,
select:focus {
  border: 0 !important;
  outline-width: 0 !important;
  border-top: 1px solid #6C7A89;
}
select {
  height: 45px;
  width: 330px;
  cursor: pointer;
}
button {
  color: #fff;
  background: #5D8CAE;
  border: 0;
  margin: 10px;
  height: 45px;
  width: 330px;
  font-size: 16px;
  font-family: "Roboto Regular" !important;
  cursor: pointer;
}
button:hover {
  background: #000;
}
.logo,
.register-logo {
  width: auto;
  height: auto;
  margin-top: -10px;
}
.logo {
  max-height: 100px;
  max-width: 100px;
  margin-bottom: 5px;
}
.register-logo {
  max-height: 50px;
  max-width: 50px;
}
.university {
  font-size: 10px;
  line-height: 15px;
  color: #7f8c8d;
  margin-bottom: 25px;
}
.header,
.head {
  margin-bottom: 20px;
  color: #6C7A89;
  font-size: 23px;
}
.header {
  margin-top: 35px;
}
.head {
  margin-top: 20px;
}
.not-registered {
  margin-top: 7px;
}
.not-registered span {
  color: #7f8c8d;
  font-size: 13px;
}
.not-registered a {
  text-decoration: none;
  color: #5D8CAE;
  font-weight: bold;
  font-size: 14px;
}
.fa {
  display: inline-block;
  font: normal normal normal 20px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 19px;
}
#errors-modal {
  width: 300px;
  max-height: 350px;
  overflow-y: auto;
}
#errors-modal h3 {
  display: inline-block;
}
#errors-modal h4 {
  padding: 0 5px 5px;
  margin: 0 5px 5px;
  white-space: pre-line;
}
#errors-modal .fa {
  padding-right: 5px;
  font-size: 20px;
  color: #D24D57;
}
#errors-modal button {
  width: 70px;
  height: 30px;
  font-size: 15px;
  float: right;
}
#register-info {
  width: 350px;
  max-height: 300px;
  overflow-y: auto;
}
#register-info .info-body {
  padding: 10px;
}
#register-info .button-holder {
  height: 40px;
}
#register-info h3 {
  color: #6C7A89;
}
#register-info button {
  float: right;
  height: 40px;
  width: 90px;
}
